import { Component, Input, OnInit } from '@angular/core';

@Component({
  selector: 'app-pie',
  templateUrl: './pie.component.html',
  styleUrls: ['./pie.component.scss']
})
export class PieComponent implements OnInit {

  @Input('dataSource')
  dataSource: any[];
  
  @Input('xName')
  xName: string;

  @Input('yName')
  yName: string;

  @Input('title')
  title: string;

  @Input('labelName')
  labelName: string;

  @Input('innerRadius')
  innerRadius: string;

  chartLabel: any;

  legend: any;

  tooltip: any;

  constructor() { }

  ngOnInit(): void {
    this.chartLabel = {
      visible: true,
      position: 'Inside',
      name: this.labelName
    }

    this.legend = {
      visible: true,
      position: 'Bottom',
      height: '8%',
      width: '35%'
    }

    this.tooltip = {
      enable: true,
      format: '${point.x}: <b>${point.y}%</b>'
    }
  }

}
