import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { 
  ChartModule,
  AccumulationChartModule,
  LineSeriesService,
  CategoryService,
  LegendService,
  DataLabelService,
  TooltipService,
  PieSeriesService,
  AccumulationDataLabelService,
  AccumulationLegendService,
  AccumulationTooltipService
} from '@syncfusion/ej2-angular-charts';

import { LineSeriesComponent } from './line-series/line-series.component';
import { PieComponent } from './pie/pie.component';



@NgModule({
  declarations: [
    LineSeriesComponent,
    PieComponent
  ],
  imports: [
    CommonModule,
    ChartModule,
    AccumulationChartModule
  ],
  providers: [
    LineSeriesService,
    CategoryService,
    LegendService,
    DataLabelService,
    TooltipService,
    PieSeriesService,
    AccumulationDataLabelService,
    AccumulationLegendService,
    AccumulationTooltipService
  ],
  exports: [
    LineSeriesComponent,
    PieComponent
  ]
})
export class SyncfusionChartModule { }
