import { Component, Input, OnInit } from '@angular/core';

@Component({
  selector: 'app-line-series',
  templateUrl: './line-series.component.html',
  styleUrls: ['./line-series.component.scss']
})
export class LineSeriesComponent implements OnInit {

  @Input('dataSource')
  dataSource: any[];

  xAxis;
  yAxis;

  chartTitle: string;
  legend;

  markerSettings;

  tooltipSettings;

  titleStyle;

  constructor() { }

  ngOnInit(): void {
    this.xAxis = {
      valueType: 'Category'
    }

    this.yAxis = {
      labelFormat: '{value}'
    }

    this.chartTitle = 'Sales Analysis';

    this.legend = {
      visible: true
    };

    this.markerSettings = {
      visible: true,
      dataLabel: {
        visible: true
      }
    };

    this.tooltipSettings = {
      enable: true
    }
  }

}
